<template>
  <div class="carousel">
    <el-main>
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        @selection-change="selectionChange"
      >
        <el-table-column
          prop="select"
          label=""
          width="50"
          show-overflow-tooltip
          type="selection"
        >
        </el-table-column>
        <el-table-column
          prop=""
          label="#"
          width="100"
          type="index"
          :index="formatter"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="img"
          label="缩略图"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <img :src="scope.row.img" alt="" class="imgItem">
          </template>
        </el-table-column>
        <el-table-column
          prop="sort"
          label="顺序"
          width="150"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="isShow"
          label="状态"
          width="100"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.isShow == true">启用</el-tag>
            <el-tag type="danger" v-if="scope.row.isShow  == false">禁用</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="标题"
          width="200"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="edit(scope.row._id)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="del(scope.row._id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <!-- Form   添加-->
      <el-button type="primary" @click="FormVisible = true">添加</el-button>
      <el-dialog title="添加" :visible.sync="FormVisible">
        <el-form :model="formData">
          <el-form-item label="标题" :label-width="formLabelWidth">
            <el-input v-model="formData.title" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="跳转链接" :label-width="formLabelWidth">
            <el-input v-model="formData.url" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="顺序" :label-width="formLabelWidth">
            <el-input v-model="formData.sort" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="状态" :label-width="formLabelWidth">
            <el-switch
              v-model="formData.isShow"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :change="changeIsShow"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="缩略图" class="avatar-uploader">
            <el-upload
              class="avatar-uploader"
              action="https://upload-z1.qiniup.com"
              :show-file-list="false"
              :on-success="upload"
              :data="{token:token}"
            >
              <img v-if="formData.img" :src="formData.img" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="FormVisible = false">取 消</el-button>
          <el-button type="primary" @click="addCarousels">确 定</el-button>
        </div>
      </el-dialog>
      <el-button type="primary" @click="toggleSelection(multiSelection)">批量删除</el-button>
      <el-pagination
        background
        layout="prev, pager, next,jumper"
        :total="total"
        :current-page="page"
        :page-size="pageSize"
        @current-change="pageChange"
      >
      </el-pagination>
    </el-footer>
  </div>
</template>
<script>
    export default{
      name: "",
      data(){
        return{
          tableData:[],
          //选择器
          multiSelection:[],
          imgId:[],
          //弹框
          FormVisible: false,
          formData: {
            img: '',
            title:'',
            url:'',
            sort:'',
            isShow:true
          },
          formLabelWidth: '120px',
          //
          token:'',
          //分页器
          pageSize:5,
          page:1,
          total:10
        }
      },
      methods:{
        //获取轮播图列表
        getCarousels(){
          this.$axios.get('getCarousels').then( res => {
            this.tableData = res.data
          })
        },
        //添加轮播图
        getToken(){
          this.$axios.qiniuGet().then( res => {
            this.token =res.data
          })
        },
        //数据回传
        upload(res){
          this.formData.img = res.url
        },
        addCarousels() {
          this.$axios.post('addCarousels',this.formData).then( res => {
            if(res.code == 200){
              this.$message({
                type:'success',
                message:'添加成功'
              });
              setTimeout(() => {
                this.$router.go(0)
              },100)
            }else{
              this.$message({
                type:'error',
                message:'添加失败'
              });
            }
          })
        },
        changeIsShow(){
          if(this.formData.isShow == true){
              this.formData.isShow = false
          }else{
            this.formData.isShow = true
          }
        },
        //编辑
        edit(id){
//          console.log(id)
          this.$router.push({path:'/carousel/update',query:{id} })
        },
        //删除
        del(id){
//          console.log(id)
          this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$axios.post('delCarousels', {id}).then(res => {
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }else{
                this.$message({
                  type: 'error',
                  message: '删除失败!'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'warning',
              message: '已取消删除'
            });
          })
        },
        //批量选择
        selectionChange(val){
          this.multiSelection = val
//          console.log(val)
        },
        toggleSelection(rows){
          if(rows){
            for(var key in rows){
//                  console.log(key)
//                this.userId = rows[key]._id
              this.imgId.push(rows[key]._id)
            }
//            console.log(this.userId)
            this.$axios.post('delManyCarousels', {id:this.imgId}).then( res => {
              if(res.code == 200) {
                this.$message({
                  type:'success',
                  message:'批量删除成功'
                })
                setTimeout( () => {
                    this.$router.go(0)
                },1000)
              }
            })
          }else {
            this.$message({
              type:'warning',
              message:'没有选择'
            })
          }
        },
        //底部分页器
        //这个时候利用formatter就可以实现索引累加啦
        formatter(index){
          //放回索引
          return index + (this.page -1) *this.pageSize +1
        },
        pageChange(val){
//          console.log(`当前页: ${val}`);
          this.page = val
          this.$axios.get('getCarousels',{page:val, pageSize:this.pageSize}).then( res => {
            this.tableData = res.data
          })
        },
        //获取数据库文档总数
        getNumber(){
          this.$axios.get('getCarouselsNum').then( res => {
            this.total = res.data
          })
        },
      },
      created(){
        this.getCarousels();
        this.getToken();
        this.getNumber()
      }
    }
</script>
<style scope>
  .carousel {
    width: 100%;
    height:100%;
    padding-top: 2px;
    padding-left: 20px;
  }
  /*内容区*/
  .carousel .el-main{
    margin:10px 22px 10px 0;
    padding: 0 14px;
    border:1px solid #dfe7ed;
  }
  .carousel .el-table .has-gutter .cell{
    color:#000;
  }
  /*列表缩略图大小*/
  .imgItem{
    width: 50px;
    height: 50px;
  }
  /*弹框*/
  .carousel .el-header .el-form-item .el-input{
    margin: 0;
  }
  /*表单上传*/
  .avatar-uploader .el-form-item__label{
    width: 120px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  /*设置上传图片的大小*/
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  /*底部*/
  .carousel .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .carousel .el-button{
    margin:10px 0 0 10px;
  }
  .carousel .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .carousel .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>



